import React from 'react';
import { Fornecedor } from '@/services/api/fornecedores.service';
import Button from '@/components/ui/Button';
import ButtonCard from '../ui/button/ButtonCard';

type FornecedorCardProps = {
  fornecedor: Fornecedor;
  onEdit: () => void;
  onDelete: () => void;
};

const FornecedorCard: React.FC<FornecedorCardProps> = ({ fornecedor, onEdit, onDelete }) => {
  return (
    <div 
    // className="bg-white dark:bg-gray-800 rounded-lg shadow-sm border border-gray-200 dark:border-gray-700">
    className="bg-white dark:bg-gray-800 rounded-lg shadow-sm hover:shadow-md shadow-gray-200 dark:shadow-gray-700 border border-gray-200 dark:border-gray-700 p-4">
      <div>
        <div className="flex justify-between items-start">
          <div>
            <h3 className="font-medium text-gray-900 dark:text-white">
              {fornecedor.nome}
            </h3>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              CNPJ: {fornecedor.cpfCnpj}
            </p>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              Email: {fornecedor.email}
            </p>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              Tel: {fornecedor.celular || 'N/A'}
            </p>
          </div>
          <div className="flex gap-2">
            {/* <Button
              variant="secondary"
              size="xs"
              onClick={onEdit}
              title="Editar fornecedor"
            >
              Editar
            </Button>
            <Button
              variant="danger"
              size="xs"
              onClick={onDelete}
              title="Excluir fornecedor"
            >
              Excluir
            </Button> */}
            <ButtonCard
            size="xs"
            variant="secondary"
            onClick={onEdit}
            title="Editar fornecedor"
            icon="edit" />

          <ButtonCard
            size="xs"
            variant="secondary"
            onClick={onDelete}
            title="Excluir fornecedor"
            icon="delete" />
          </div>
        </div>

      </div>
    </div>
  );
};

export default FornecedorCard;
