import React, { useState, useEffect } from 'react';
import { toast } from 'react-hot-toast';
import Input from '@/components/form/input/InputField';
import Label from '@/components/form/Label';
import Button from '@/components/ui/Button';
import { Fornecedor, CreateFornecedorDTO } from '@/services/api/fornecedores.service';
import { services } from '@/services/api';
import { CategoriaFornecedor } from '@/services/api/categoria-fornecedores.service';

type FormErrors = {
  nome?: string;
  cpfCnpj?: string;
  email?: string;
  celular?: string;
};

type FornecedorFormProps = {
  fornecedor?: Fornecedor;
  onSubmit: (data: CreateFornecedorDTO) => Promise<void>;
  onCancel: () => void;
};

const FornecedorForm: React.FC<FornecedorFormProps> = ({ fornecedor, onSubmit, onCancel }) => {
  const [formData, setFormData] = useState<CreateFornecedorDTO>({
    nome: fornecedor?.nome || '',
    cpfCnpj: fornecedor?.cpfCnpj || '',
    email: fornecedor?.email || '',
    celular: fornecedor?.celular || '',
    categoriaId: fornecedor?.categoriaId || null,
  });
  
  const [errors, setErrors] = useState<FormErrors>({});
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [categorias, setCategorias] = useState<CategoriaFornecedor[]>([]);

  useEffect(() => {
    const loadCategorias = async () => {
      try {
        const response = await services.categoriaFornecedor.list();
        setCategorias(response.data.data);
      } catch (error) {
        toast.error('Erro ao carregar categorias');
      }
    };
    loadCategorias();
  }, []);

  const validateForm = () => {
    const newErrors: FormErrors = {};

    if (!formData.nome) {
      newErrors.nome = 'Nome é obrigatório';
    }

    if (!formData.cpfCnpj) {
      newErrors.cpfCnpj = 'CPF ou CNPJ é obrigatório';
    }

    // if (!formData.email) {
    //   newErrors.email = 'Email é obrigatório';
    // }

    // if (!formData.celular) {
    //   newErrors.celular = 'Celular é obrigatório';
    // }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!validateForm()) return;

    setIsSubmitting(true);
    try {
      await onSubmit(formData);
    } catch (error) {
      toast.error('Erro ao salvar fornecedor');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div className="md:col-span-2">
          <Label htmlFor="nome">
            Nome<span className="text-error-500">*</span>
          </Label>
          <Input
            id="nome"
            name="nome"
            value={formData.nome}
            onChange={(e) => setFormData({ ...formData, nome: e.target.value })}
            error={errors.nome}
          />
        </div>

        <div>
          <Label htmlFor="cpfCnpj">
            CPF ou CNPJ<span className="text-error-500">*</span>
          </Label>
          <Input
            id="cpfCnpj"
            name="cpfCnpj"
            value={formData.cpfCnpj}
            onChange={(e) => setFormData({ ...formData, cpfCnpj: e.target.value })}
            error={errors.cpfCnpj}
          />
        </div>

        <div>
          <Label htmlFor="email">
            Email
            {/* <span className="text-error-500">*</span> */}
          </Label>
          <Input
            id="email"
            name="email"
            type="email"
            value={formData.email}
            onChange={(e) => setFormData({ ...formData, email: e.target.value })}
            error={errors.email}
          />
        </div>

        <div>
          <Label htmlFor="telefone">
            Telefone
            {/* <span className="text-error-500">*</span> */}
          </Label>
          <Input
            id="telefone"
            name="telefone"
            value={formData.celular}
            onChange={(e) => setFormData({ ...formData, celular: e.target.value })}
            error={errors.celular}
          />
        </div>

        <div>
          <Label htmlFor="categoria">Categoria
            <span className="text-error-500">*</span>
          </Label>
          <select
            id="categoria"
            name="categoria"
            value={formData.categoriaId || ''}
            onChange={(e) => setFormData({
              ...formData,
              categoriaId: e.target.value ? Number(e.target.value) : null
            })}
            className="w-full rounded-lg border border-gray-300 bg-white px-3 py-2 text-sm dark:border-gray-700 dark:bg-gray-800 dark:text-gray-300"
          >
            <option value="">Selecione uma categoria</option>
            {categorias.map((categoria) => (
              <option key={categoria.id} value={categoria.id}>
                {categoria.nome}
              </option>
            ))}
          </select>
        </div>

      </div>

      <div className="flex justify-end space-x-4 pt-4">
        <Button
          variant="secondary"
          onClick={onCancel}
          type="button"
        >
          Cancelar
        </Button>
        <Button
          variant="primary"
          type="submit"
          loading={isSubmitting}
        >
          {fornecedor ? 'Atualizar' : 'Criar'}
        </Button>
      </div>
    </form>
  );
};

export default FornecedorForm;
