import React from 'react';
import { CategoriaFornecedor } from '@/services/api/categoria-fornecedores.service';
import Button from '@/components/ui/Button';

type CategoriaFornecedorCardProps = {
  categoria: CategoriaFornecedor;
  onEdit: () => void;
  onDelete: () => void;
};

const CategoriaFornecedorCard: React.FC<CategoriaFornecedorCardProps> = ({ 
  categoria, 
  onEdit, 
  onDelete 
}) => {
  return (
    <div className="relative overflow-hidden rounded-lg border border-gray-200 bg-white p-4 dark:border-gray-800 dark:bg-gray-900">
      <div className="flex justify-between items-start">
        <div>
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-2">
            {categoria.nome}
          </h3>
        </div>
        <div className="flex gap-2">
          <Button
            variant="secondary"
            size="sm"
            onClick={onEdit}
            title="Editar categoria"
          >
            Editar
          </Button>
          <Button
            variant="danger"
            size="sm"
            onClick={onDelete}
            title="Excluir categoria"
          >
            Excluir
          </Button>
        </div>
      </div>
    </div>
  );
};

export default CategoriaFornecedorCard;
