"use client";

import React, { useState, useEffect, useCallback } from "react";
import { services } from "@/services/api";
import { useAuth } from "@/hooks/useAuth";
import toast from "react-hot-toast";
import Button from "@/components/ui/Button";
import SearchInput from "@/components/form/input/SearchInput";
import { Modal } from "@/components/ui/modal";
import FornecedorCard from "@/components/fornecedores/FornecedorCard";
import FornecedorForm from "@/components/fornecedores/FornecedorForm";
import CategoriaFornecedorCard from "@/components/fornecedores/CategoriaFornecedorCard";
import CategoriaFornecedorForm from "@/components/fornecedores/CategoriaFornecedorForm";

import {
  Fornecedor,
  CreateFornecedorDTO,
} from "@/services/api/fornecedores.service";
import {
  CategoriaFornecedor,
  CreateCategoriaFornecedorDTO,
} from "@/services/api/categoria-fornecedores.service";

type View = "FORNECEDORES" | "CATEGORIAS";

export default function FornecedoresPage() {
  const { user: currentUser } = useAuth();

  const [fornecedores, setFornecedores] = useState<Fornecedor[]>([]);
  const [categorias, setCategorias] = useState<CategoriaFornecedor[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState("");
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedFornecedor, setSelectedFornecedor] =
    useState<Fornecedor | null>(null);
  const [selectedCategoria, setSelectedCategoria] =
    useState<CategoriaFornecedor | null>(null);
  const [activeView, setActiveView] = useState<View>("FORNECEDORES");
  const [selectedCategoryFilter, setSelectedCategoryFilter] = useState<
    number | null
  >(null);

  const itemsPerPage = 9;

  // useEffect(() => {
  //   console.log("selectedCategoria mudou:", selectedCategoria);
  //   console.log("activeView mudou:", activeView);
  // }, [activeView]);

  // Carregar categorias
  const loadCategorias = useCallback(async () => {
    try {
      const response = await services.categoriaFornecedor.list();
      setCategorias(response.data.data);
    } catch (error) {
      toast.error("Erro ao carregar categorias");
    }
  }, []);

  // Carregar fornecedores
  const loadFornecedores = useCallback(async () => {
    try {
      setLoading(true);
      const response = await services.fornecedor.list({
        page,
        limit: itemsPerPage,
        search: searchTerm,
      });
      const data = response.data.data;
      setFornecedores(data);
      setTotalPages(Math.ceil((data?.length || 0) / itemsPerPage));
    } catch (error) {
      toast.error("Erro ao carregar fornecedores");
    } finally {
      setLoading(false);
    }
  }, [page, searchTerm, itemsPerPage]);

  useEffect(() => {
    loadCategorias();
  }, [loadCategorias]);

  useEffect(() => {
    loadFornecedores();
  }, [loadFornecedores]);

  // Editar fornecedor
  const handleEdit = (fornecedor: Fornecedor) => {
    setSelectedFornecedor(fornecedor);
    setIsModalOpen(true);
  };

  // Excluir fornecedor
  const handleDelete = async (id: number) => {
    if (!window.confirm("Tem certeza que deseja excluir este fornecedor?"))
      return;
    try {
      await services.fornecedor.delete(id);
      toast.success("Fornecedor excluído com sucesso");
      loadFornecedores();
    } catch (error) {
      toast.error("Erro ao excluir fornecedor");
    }
  };

  // Excluir categoria
  const handleDeleteCategoria = async (id: number) => {
    if (!window.confirm("Tem certeza que deseja excluir esta categoria?"))
      return;
    try {
      await services.categoriaFornecedor.delete(id);
      toast.success("Categoria excluída com sucesso");
      loadCategorias();
      loadFornecedores();
    } catch (error) {
      toast.error("Erro ao excluir categoria");
    }
  };

  // Salvar fornecedor
  const handleSave = async (data: CreateFornecedorDTO) => {
    try {
      if (selectedFornecedor) {
        await services.fornecedor.update(selectedFornecedor.id, data);
        toast.success("Fornecedor atualizado com sucesso");
      } else {
        await services.fornecedor.create(data);
        toast.success("Fornecedor criado com sucesso");
      }
      setIsModalOpen(false);
      setSelectedFornecedor(null);
      loadFornecedores();
    } catch (error) {
      toast.error("Erro ao salvar fornecedor");
    }
  };

  // Salvar categoria
  const handleSaveCategoria = async (data: CreateCategoriaFornecedorDTO) => {
    try {
      if (selectedCategoria) {
        await services.categoriaFornecedor.update(selectedCategoria.id, data);
        toast.success("Categoria atualizada com sucesso");
      } else {
        await services.categoriaFornecedor.create(data);
        toast.success("Categoria criada com sucesso");
      }
      setIsModalOpen(false);
      setSelectedCategoria(null);
      loadCategorias();
    } catch (error) {
      toast.error("Erro ao salvar categoria");
    }
  };

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      {/* Cabeçalho */}
      <div className="mb-8">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-2xl font-semibold text-gray-900 dark:text-white">
              Fornecedores e Categorias
            </h1>
            <p className="mt-1 text-sm text-gray-500 dark:text-gray-400">
              Gerencie seus fornecedores e categorias
            </p>
          </div>

          <Button
            variant="primary"
            onClick={() => {
              setSelectedFornecedor(null);
              setSelectedCategoria(null);
              setIsModalOpen(true);
            }}
          >
            {activeView === "FORNECEDORES"
              ? "Novo Fornecedor"
              : "Nova Categoria"}
          </Button>
        </div>

        {/* Filtros */}
        <div className="mt-6">
          {/* Botões de troca de view */}
          <div className="flex flex-wrap gap-2 mb-6">
            {["FORNECEDORES", "CATEGORIAS"].map((view) => (
              <button
                key={view}
                onClick={() => {
                  setActiveView(view as View);
                  if (view === "CATEGORIAS") {
                    setSelectedCategoryFilter(null);
                  }
                }}
                className={`px-4 py-2 text-sm font-medium rounded-lg transition-colors
                  ${
                    activeView === view
                      ? "bg-brand-500 text-white dark:bg-brand-400 dark:text-gray-900"
                      : "bg-gray-100 text-gray-600 hover:bg-gray-200 dark:bg-gray-800 dark:text-gray-400 dark:hover:bg-gray-700"
                  }`}
              >
                {view === "FORNECEDORES" ? "Fornecedores" : "Categorias"}
              </button>
            ))}
          </div>

          {/* Busca e filtro de categoria */}
          <div className="flex flex-col sm:flex-row gap-4 items-center justify-between mb-6">
            <div className="flex gap-4 w-full sm:w-auto">
              <div className="w-full sm:w-96">
                <SearchInput
                  value={searchTerm}
                  onChange={(e) => {
                    setSearchTerm(e.target.value);
                    setPage(1);
                  }}
                  placeholder={`Buscar ${
                    activeView === "FORNECEDORES" ? "fornecedores" : "categorias"
                  }...`}
                />
              </div>

              {activeView === "FORNECEDORES" && (
                <select
                  title="Selecione uma categoria"
                  className="w-48 rounded-lg border border-gray-300 bg-white px-3 py-2 text-sm dark:border-gray-700 dark:bg-gray-800 dark:text-gray-300"
                  onChange={(e) => {
                    const categoryId = e.target.value
                      ? Number(e.target.value)
                      : null;
                    setSelectedCategoryFilter(categoryId);
                    setPage(1);
                  }}
                  value={selectedCategoryFilter || ""}
                >
                  <option value="">Todas as categorias</option>
                  {categorias.map((categoria) => (
                    <option key={categoria.id} value={categoria.id}>
                      {categoria.nome}
                    </option>
                  ))}
                </select>
              )}
            </div>

            {/* Paginação */}
            {totalPages > 1 && (
              <div className="flex gap-2">
                {Array.from({ length: totalPages }, (_, i) => i + 1).map(
                  (num) => (
                    <button
                      key={num}
                      onClick={() => setPage(num)}
                      className={`px-3 py-1 text-sm font-medium rounded transition-colors
                        ${
                          page === num
                            ? "bg-brand-500 text-white dark:bg-brand-400"
                            : "bg-gray-100 text-gray-600 hover:bg-gray-200 dark:bg-gray-800 dark:text-gray-400 dark:hover:bg-gray-700"
                        }`}
                    >
                      {num}
                    </button>
                  )
                )}
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Lista */}
      {loading ? (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {[1, 2, 3].map((n) => (
            <div
              key={n}
              className="animate-pulse bg-gray-100 dark:bg-gray-800 rounded-lg h-48"
            />
          ))}
        </div>
      ) : activeView === "FORNECEDORES" ? (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {fornecedores.length > 0 ? (
            fornecedores
              .filter(
                (fornecedor) =>
                  !selectedCategoryFilter ||
                  fornecedor.categoriaId === selectedCategoryFilter
              )
              .map((fornecedor) => (
                <FornecedorCard
                  key={fornecedor.id}
                  fornecedor={fornecedor}
                  onEdit={() => handleEdit(fornecedor)}
                  onDelete={() => handleDelete(fornecedor.id)}
                />
              ))
          ) : (
            <div className="col-span-full text-center py-12">
              <p className="text-gray-500 dark:text-gray-400">
                Nenhum fornecedor encontrado.
              </p>
            </div>
          )}
        </div>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {categorias.length > 0 ? (
            categorias.map((categoria) => (
              <CategoriaFornecedorCard
                key={categoria.id}
                categoria={categoria}
                onEdit={() => {
                  setSelectedCategoria(categoria);
                  setIsModalOpen(true);
                }}
                onDelete={() => handleDeleteCategoria(categoria.id)}
              />
            ))
          ) : (
            <div className="col-span-full text-center py-12">
              <p className="text-gray-500 dark:text-gray-400">
                Nenhuma categoria encontrada.
              </p>
            </div>
          )}
        </div>
      )}

      {/* Modal */}
      <Modal
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false);
          setSelectedFornecedor(null);
          setSelectedCategoria(null);
        }}
        className="max-w-[800px] m-4"
      >
        <div className="no-scrollbar relative w-full overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-11">
          <div className="px-2 pr-14">
            <h4 className="mb-2 text-2xl font-semibold text-gray-800 dark:text-white/90">
              {activeView === "FORNECEDORES"
                ? selectedFornecedor
                  ? "Editar Fornecedor"
                  : "Novo Fornecedor"
                : selectedCategoria
                ? "Editar Categoria"
                : "Nova Categoria"}
            </h4>
            <p className="mb-6 text-sm text-gray-500 dark:text-gray-400 lg:mb-7">
              {activeView === "FORNECEDORES"
                ? selectedFornecedor
                  ? "Atualize as informações do fornecedor."
                  : "Preencha os dados para criar um novo fornecedor."
                : selectedCategoria
                ? "Atualize as informações da categoria."
                : "Preencha os dados para criar uma nova categoria."}
            </p>
          </div>

          {activeView === "FORNECEDORES" ? (
            <FornecedorForm
              fornecedor={selectedFornecedor || undefined}
              onSubmit={handleSave}
              onCancel={() => {
                setIsModalOpen(false);
                setSelectedFornecedor(null);
              }}
            />
          ) : (
            <CategoriaFornecedorForm
              categoria={selectedCategoria || undefined}
              onSubmit={handleSaveCategoria}
              onCancel={() => {
                setIsModalOpen(false);
                setSelectedCategoria(null);
              }}
            />
          )}
        </div>
      </Modal>
    </div>
  );
}
